@import "/src/variable.scss";

.container {
    position: relative;
    width: fit-content;
}

.audioWrap {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
    pointer-events: none;
}
.controlWrap {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 0 24px 25px;
    z-index: 10;
    transform: translate3d(0, 0, 0);
    .playBtn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border: none;
        background-color: initial;
        width: 76px;
        height: 76px;
        background-size: cover;
        &.play {
            background-image: url("./assets/btn_video_stop.png");
        }
        &.pause {
            background-image: url("./assets/btn_video_play.png");
        }
        img {
            width: 100%;
            height: 100%;
        }
    }
    .fullScreenBtn {
        width: 44px;
        height: 44px;
        background-color: transparent;
        border: none;
        margin: 0 0 9px auto;
        background-image: url("./assets/ic_full_screen.svg");
        background-size: cover;
        &.full {
            background-image: url("./assets/ic_minimum_screen.svg");
        }
    }
    .durationBar {
        position: relative;
        // display: flex;
        // align-items: center;
        input[type="range"] {
            width: 100%;
            -webkit-appearance: none;
            appearance: none;
            position: relative;
            z-index: 2;
            width: 100%;
            // &::-moz-range-thumb,
            &::-webkit-slider-runnable-track {
                -webkit-appearance: none;
                appearance: none;
                height: 4px;
            }
            &::-webkit-slider-thumb {
                -webkit-appearance: none;
                appearance: none;
                border: none;
                width: 18px;
                height: 18px;
                background-color: white;
                border-radius: 50%;
                border: 4px solid $--primary;
                position: relative;
                margin: -7px 0;
                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    width: 5px;
                    height: 5px;
                }
            }
        }
        .timeline {
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            transform: translateY(-50%);
            -webkit-transform: translateY(-50%);
            background-color: $--disabled;
            height: 4px;
            .runningTime {
                width: 0px;
                height: 100%;
                background-color: $--primary;
            }
        }
    }
}
